body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.landing-page {
  text-align: center;
  padding-top: 10px;
  padding-bottom: 50px;
  background-color: #fc8d15;
  height: max-content;
  scroll-behavior: smooth;
}
.landing-page * {
  transition: all 0.3s ease;
}

.landing-page h1 {
  font-size: 2.5rem;
  color: black;
  font-family: monospace;
}

.landing-page p {
  font-size: 1.2rem;
  color: black;
  margin-bottom: 20px;
  font-weight: bold;
}

.hero-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 50px 0;
}

.hero-content {
  flex: 1;
  padding-right: 50px; 
}

.hero-content h2 {
  font-size: 36px;
  margin-bottom: 40px;
  font-family: monospace;
  color: black;
}

.hero-content p {
  font-size: 18px;
  color: black;
  line-height: 1.5;
  padding-left: 20px;
}

.hero-image img {
  max-width: 100%;
  height: auto;
  border-radius: 20px;
  padding-right: 100px; 
}

@media (max-width: 768px) {
  .hero-section {
    flex-direction: column;
    text-align: center;
  }
  .hero-content {
    padding-right: 0;
    margin-bottom: 30px;
  }
}
.popular-recipes-section {
  background-color: white;
}
.popular-recipes-section h2 {
  color: black;
  font-family: monospace;
  font-weight: 600;
  font-size: 30px;
  padding-top: 25px;
}

.recipee-cards {
  display: flex; 
  justify-content: space-between; 
  padding-left: 20px;
  padding-right: 20px;
}

.recipe-cards {
  width: calc(50% - 10px); 
  margin-right: 20px; 
  padding: 40px; 
  border: 1px solid transparent;
  border-radius: 5px; 
}

.recipe-cards:last-child {
  margin-right: 0; 
}

.recipe-cards img {
  max-width: 100%;
  height: auto;
  border-radius: 5px; 
}

.recipe-cards h3 {
  margin-top: 20px; 
  color: black;
  font-family: monospace;
}

.recipe-cards p {
  margin-bottom: 0;
  font-size: 14px;
  color: black;
}

.about-section {
  padding: 30px; 
  background-color: #222;
}

.about-section h2 {
  font-size: 24px; 
  color: white; 
  margin-bottom: 30px; 
  font-family: monospace;

}

.about-section p {
  font-size: 16px; 
  color: white; 
  line-height: 1.6; 
  margin-bottom: 45px; 
}

.about-section ul {
  list-style-type: none; 
  margin-left: 20px; 
}

.about-section ul li {
  font-size: 16px; 
  color: white; 
  margin-bottom: 10px;
  
}

.footer {
  background-color: white; 
  color: black; 
}

.footer-content {
  display: flex; 
  justify-content: space-between; 
  max-width: 1200px; 
  margin: 0 auto; 
}

.footer-section {
  flex: 1; 
  margin-right: 30px; 
}

.footer-section h3 {
  font-size: 20px; 
  margin-bottom: 20px; 
  font-family: monospace;
}

.footer-section ul {
  list-style-type: none;
  padding: 0; 
}

.footer-section ul li {
  font-size: 16px; 
  margin-bottom: 10px; 
}

.footer-bottom {
  background-color: #222; 
  padding: 20px; 
  text-align: center;
}

.footer-bottom p {
  margin: 0; 
  font-size: 14px; 
  color: white;
}


/*  NavBar Styling */

nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 70px;
  background-color: rgba(0, 0, 0, 0.431);
}

nav ul li {
  margin: 0 10px;
  font-family: 'Times New Roman', Times, serif;
}
.navigation-link {
  text-decoration: none;
  color: #333;
  display: grid;
  font-weight: bold;
  padding: 8px 12px;
  margin-right: 10px;
  border-radius: 5px;
  background-color: #007bff;
  transition: background-color 0.3s ease;
}

nav ul li a {
  color: white;
  text-decoration: none;
  font-size: 20px;
  padding: 10px;

}

nav ul li a:hover {
  background-color: #222;
  color: #fff;
  border-radius: 5px;
}

nav ul li a.active {
  color: #fff;
  background-color: #333;
}
.navigation-container {
  background-image: url(https://res.cloudinary.com/dswjeyq4u/image/upload/v1712507951/pexels-photo-616401_ebmqkr.jpg);
  background-size: cover; 
  background-position: center; 
  background-repeat: no-repeat;
}

/* LoginForm.css */

.login-container {
  background-color: rgb(151, 151, 151); 
  padding: 40px; 
  height: 100vh;
}

.login-form {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: rgba(17, 24, 39, 1);
  height: 50vh;
}
.input-field {
  width: 90%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: rgba(17, 24, 39, 1);
}

.login-form h2 {
  margin-bottom: 20px;
  text-align: center;
  color: white;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.login-form label {
  color: white;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin-bottom: 15px
}
.error-message {
  color: red;
  margin-bottom: 10px;
  text-align: center;
  font-weight: 600;
}
.submit-button {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-weight: 600;
}

.submit-button:hover {
  background-color: #0056b3;
}

