/* NavBar Select CSS */

.navbar-select {
  padding: 8px 12px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #fff;
  color: #333;
  cursor: pointer;
  transition: border-color 0.3s ease;
}

.navbar-select:hover {
  border-color: #999;
}

.navbar-select:focus {
  outline: none;
  border-color: #555;
}

.navbar-select option {
  background-color: #fff;
  color: #333;
}

.navbar-select option:hover {
  background-color: #f2f2f2;
}

.navbar-select option:checked {
  background-color: #ccc;
  color: #fff;
}

/* Add Recipe Form */

.add-recipe-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  height: 400px;
}
.add-recipe-container h2 {
  text-align: center;
}

.recipe-form {
  background-color: #f9f9f9;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.form-group {
  margin-bottom: 15px;
}

label {
  font-weight: bold;
}

input[type="text"] {
  width: 90%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

textarea[id="description"] {
  width: 90%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  height: 200px;
}

.submit-button {
  background-color: #007bff;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.submit-button:hover {
  background-color: #0056b3;
}

/* Edit Recipe CSS */

.form-container {
  max-width: 700px;
  margin-left: 600px;
  margin-top: 50px;
  padding: 20px;
  height: 500px;
  background-color: #c9c5c5;
  border-radius: 10px;
}

.form-container h2 {
  font-size: 34px;
  margin-bottom: 20px;
  text-align: center;
  font-family: monospace;
}

.form-container label {
  display: block;
  margin-bottom: 5px;
}

.form-container input[type="text"],
.form-container textarea,
.form-container select {
  width: 100%; 
  padding: 8px; 
  margin-bottom: 10px;
  border: 1px solid #ccc; 
  border-radius: 5px; 
  box-sizing: border-box;
}

.form-container button[type="submit"] {
  background-color: #4CAF50; 
  color: white; 
  padding: 10px 20px; 
  border: none; 
  border-radius: 5px; 
  cursor: pointer; 
  transition: background-color 0.3s; 
}

.form-container button[type="submit"]:hover {
  background-color: #45a049; 
}

/* All Recipe container CSS  */

.recipe-container {
  margin: 20px;
}

.recipe-container h2 {
  font-size: 28px;
  margin-bottom: 20px;
  text-align: center;
  font-family: monospace;
  margin-top: 40px;
}

.recipe-grid {
  display: flex;
  flex-wrap: wrap; 

}

.recipe-card {
  width: calc(33.33% - 10px); 
  margin: 10px; 
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 5px;
  box-shadow: 0 2px 4px rgba(0, 0.9, 0.7, 0.5); 
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.recipe-card h3 {
  font-size: 24px;
  margin-bottom: 10px;
  text-align: center;
}

.recipe-card p {
  font-size: 19px;
  margin-bottom: 10px;
  font-family: 'Times New Roman', Times, serif;
  text-align: center;
}

.recipe-card button {
  background-color: #4CAF50;
  color: white;
  padding: 8px 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.recipe-card button:hover {
  background-color: #45a049;
}

.message {
  font-size: 16px;
  color: #FF0000;
  margin-top: 10px;
}

.link {
  display: inline-block;
  margin-top: 20px;
  text-decoration: none;
  color: #007bff;
  font-weight: bold;
}


